
body {
    color: whitesmoke;
    background: #131313;
    margin: 0;
    padding: 0;
    font-family: Oswald, sans-serif;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 10px;
}

$primary: #fffa29;
//$primary: #ff6c55;
$secondary: #fffa29;
$red:red;
$blue:#5684f5;
$green:#5bf099;
$pink:#ffb3b3;


p {
    margin-top:0px;
    margin-bottom: 0px;
}

#loading-cover {
    width:100vw;
    height:100vh;
    position:absolute;  
    z-index:1000000;
    display: flex;
    background: #000000b8;
    align-content: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#master-bpm-input {
    background: none;
    border: none;
    color: yellow;
    font-weight: 700;
    padding: 5px;
    width: 100px;
}

@keyframes rotate-3d {
    from {transform: rotate3d(0, 0, 1, 0deg);}
    to {transform: rotate3d(1, 1, 1, 360deg);}
  }

.logo-wrapper-3d {
    transform: rotate3d(1, 1, 1, 50deg); 
    width:100px;
    height:100px;
    transform-style: preserve-3d;
    animation: rotate-3d 10s infinite;
    animation-timing-function: linear;

}

.logo-wrapper-3d-sm {
    transform: rotate3d(1, 1, 1, 50deg); 
    width:2vh;
    height:2vh;
    transform-style: preserve-3d;
    animation: rotate-3d 10s infinite;
    animation-timing-function: linear;
}

.primary {
    color:$primary
}
.pink {
    color:$pink;
}
.green {
    color:$green;
}
.blue {
    color:$blue;
}

.primary > button {
    background:$primary;
    border:$primary;
}
.pink > button {
    background:$pink;
    border:$pink;
}
.green > button {
    background:$green;
    border:$green;
}
.blue > button {
    background:$blue;
    border:$blue;
}

.default-button {
    color:black;
}

.default-button.primary {
    background:$primary;
    border:$primary;
}
.default-button.pink {
    background:$pink;
    border:$pink;
}
.default-button.green {
    background:$green;
    border:$green;
}
.default-button.blue {
    background:$blue;
    border:$blue;
}



button {
    color: #000;
    text-transform: uppercase;
    letter-spacing: 2px;
    background: $primary;
    border: 1px solid $primary;
    border-radius: 2px;
    margin: .3rem;
    padding: .3rem 2rem;
    font-weight: 600;
    font-size: 0.6rem;
    opacity:1;
    cursor:pointer;
}

button.secondary {
    background:$secondary !important;
    border:1px solid $secondary !important;
}

button.black {
    background:black;
    border:1px solid black;
    color:$secondary
}

button.inactive {
    opacity:0.5;
}

.d-none {
    display: none !important;
}

.d-flex {
    display: flex;
}

.d-block {
    display:block !important;
}

.flex-1 {
    flex:1;
}

.border-bottom {
    border-bottom: 1px solid rgba(255,255,255,0.2);
}

.d-flex.column {
    flex-direction: column;
}
.d-flex.row {
    flex-direction: row;
}

.justify-between {
    justify-content: space-between;
}

.align-center {
    align-items: center;
}

.mr-1 {
    margin-right:5px;
}
.mr-2 {
    margin-right:10px;
}

.ml-1 {
    margin-left:5px;
}
.ml-2 {
    margin-left: 10px;
}

.w-100 {
    width:100%;
}

.justify-center {
    justify-content: center;
}

.text-align-center {
    text-align: center;
}

.pad-array {
    display: flex;
    flex-wrap:wrap;
    width:100%;

}

.pad-array.standard-sort {
    flex-direction: row;
}
.pad-array.reverse-sort {
    flex-direction: row-reverse;
}

.top-bar {
    //height: 3vh;
    width: 100vw;
    //padding: 10px;
    background: black;
    border-bottom:1px solid rgba(255,255,255,0.3);
    display: flex;
    align-items: center;
}

#main-container {
    padding:1.5rem;
}

#right-wrapper {
    width: 49.6%;
    height: 100%;
    background: black;
    padding: 1rem;
    border-left: 1px solid rgba(255,255,255,0.2);
    overflow:hidden
}

#browser-wrapper {
    position: relative;
    overflow: scroll;
    height: 100%;
}

#browser-wrapper>div {
    height: 100%;
    position: relative;
}

#context-wrapper {
    height:100%;
}
#context-container {
    height:100%;
}

.pad-context {
    height:100%;
}

.pad-context.blue > * > button {
    background:$blue;
}

.file-drop {

    text-align: center;
    z-index: 10000;
    background: #000;
    border: 1px dashed #fff;
    border-radius: 5px;
    flex-direction: column;
    flex: 1;
    place-content: center;
    place-items: center;
    padding: .5rem;
    transition: all .3s ease-in-out;
    display: flex;
    position: absolute;
    flex: 1;
    width: 94%;
    height: 87%;
}

.file-drop:hover {
    border:1px dashed $secondary;
}

.file-drop > h3 {
    user-select: none;
    pointer-events: none;
}

.pad {
    color: #fff;
    width: 24%;
    height: 150px;
    text-transform: uppercase;
    background: #000;
    border: 2px solid #ffffff3d;
    border-radius: 3px;
    margin: .2rem;
    position: relative;
    cursor:pointer;
    flex-wrap: wrap;
    overflow: hidden;
}

.pad::before {
    content: attr(data-key);
    position: absolute;
    top: 0;
    left: 0;
    padding: 5px;
    color: rgba(255,255,255,0.3);
}

.pad > .content > h3 {
    font-size: 0.5rem;
    line-break: anywhere;
    margin-left:10%;
    user-select: none;
}

.pad:hover {
    box-shadow:0px 0px 5px rgba(0,0,0,0.7);
}

.pad.selected.primary {

    border:2px solid $primary;
    color:$primary;
    box-shadow:0px 0px 5px rgba(0,0,0,0.3);

}
.pad.selected.blue {

    border:2px solid $blue;
    color:$blue;
    box-shadow:0px 0px 5px rgba(0,0,0,0.3);

}
.pad.selected.green {

    border:2px solid $green;
    color:$green;
    box-shadow:0px 0px 5px rgba(0,0,0,0.3);

}
.pad.selected.pink {

    border:2px solid $pink;
    color:$pink;
    box-shadow:0px 0px 5px rgba(0,0,0,0.3);

}

.pad.loaded.primary {

    border:2px solid $primary;
    color:$primary;
    box-shadow:0px 0px 5px $primary;

}


.pad.loaded.pink {

    border:2px solid $pink;
    color:$pink;
    box-shadow:0px 0px 5px $pink;

}


.pad.loaded.blue {

    border:2px solid $blue;
    color:$blue;
    box-shadow:0px 0px 5px $blue;

}


.pad.loaded.green {

    border:2px solid $green;
    color:$green;
    box-shadow:0px 0px 5px $green;

}

.pad.active {
    background:blue !important;
}

// Patches

.patch-panel {
    background: rgba(0,0,0,0.6);
    margin: 0.5rem;
    border-radius: 3px;
    padding: 0px;
    border: 1px solid rgba(255,255,255,0.7);
    color: white;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 10px inset rgba(0,0,0,0.5);
  }

.patch-top-bar {
    display: flex;
    height: 1rem;
    padding: 3px;
    background: $secondary;
    color: black;
}

.patch-top-bar>h3 {
    padding: 0px;
    margin: 0px;
    flex-grow:1;
    margin-left:5px;
}

.patch-top-bar>button {
    padding: 0px 10px;
    margin: 0px;
    margin-right:5px;
}

// Effects
.effects-view-container {
    width:100%;
}

.effects-button-list {
    width:30%;
    display:flex;
}

.effects-button-wrapper {
    width: 100%;
    padding: 5px;
}

.effects-item-list {
    width:70%;
}

.effect-button {
    margin:0px;
    width:100%;
    border:1px solid black;
    opacity:1;
}

.effect-button.inactive {
    opacity:0.5;
}

// Groups 

.button-group-group {
    display: flex;
    flex-direction: column;
}

.button-group-group > button {
    width:40px;
    height:147px;
    padding:0px;
    color:black !important;

}

.grounp-button {
    width:20px;
    height:20px;
    border:none;
    padding: 0px;
    
}


.group-button.primary{
    background:$primary;
    border:$primary;
}
.group-button.blue {
    background:$blue;
    border:$blue;
}
.group-button.green {
    background:$green;
    border:$green;
}
.group-button.pink {
    background:$pink;
    border:$pink;
}

// Resources
.resource-item {

    border: 1px solid rgba(255, 255, 255, 0.2);
    margin-bottom: 5px;
    border-radius: 2px;

}

.resource-name {
    min-width: 20%;
    margin-left: 10px;
}

.resource-player {

    flex-grow: 1;
}

.resource-tags-container {

    display: flex;
}

.resource-tag {
    color: white;
    margin: 2px;
    padding: 3px;
    border: 1px solid white;
}

// Sequencer

.sequencer-grid {

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    width:600px

}

.sequencer-pad {

    width:150px;
    height:150px;
    background:none;

}

.sequencer-pad.loaded {
    background:#fffa2991;
}

.sequence-active {
    background:$primary !important;
}